* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 4rem;
    height: 100%;
    background: linear-gradient(121deg, #afd275, #afd275);
    /* background-color: #afd275; */
}

input {
    border: none;
    outline: none;
    background: none;
    font-size: 18px;
    color: #555;
    padding: 20px 20px 20px 20px;
    /* width: 100%; */
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25),
        -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
    width: 300px;
    border-radius: 40px;
}
h2 {
    text-align: left;

    font-size: 2.5rem;
    color: #fff;
    text-shadow: 0 1px #808d93, -1px 0 #cdd2d5, -1px 2px #808d93,
        -2px 1px #cdd2d5, -2px 3px #808d93, -3px 2px #cdd2d5, -3px 4px #808d93,
        -4px 3px #cdd2d5, -4px 5px #808d93, -5px 4px #cdd2d5, -5px 6px #808d93,
        -6px 5px #cdd2d5, -6px 7px #808d93, -7px 6px #cdd2d5, -7px 8px #808d93,
        -8px 7px #cdd2d5;
}
p {
  font-size: 1.2rem;

}

hr {
    margin: 30px 0 0 0;
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25),
        -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
    border-color: rgba(255, 255, 255, 0.397);
    background: linear-gradient(
        -45deg,
        rgba(0, 0, 0, 0.22),
        rgba(255, 255, 255, 0.25)
    );
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
}

.card {
    align-content: center;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-content: center;
    margin: 20px;
    border-radius: 40px;
    background: linear-gradient(
        -45deg,
        rgba(0, 0, 0, 0.22),
        rgba(255, 255, 255, 0.25)
    );
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25),
        -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
    width: 300px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
}
